  /* DEFAULT LINKS */
  /* CLICKABLE CARD */
  /* OUTLINE */
}
body a:link, body a:visited {
  display: inline-block;
  color: currentColor;
  text-decoration: none;
  transition: var(--all-3);
}
body a:hover, body a:active {
  color: inherit;
}
body .clickable-card:not(a) {
  position: static;
}
body .clickable-card:not(a) a {
  position: static;
}
body .clickable-card:not(a) > a:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}
body a.clickable-card {
  position: static;
}
body a.clickable-card:after {
  content: "";
  position: absolute;
  inset: 0;
  cursor: pointer;
  display: flex;
}
body :focus,
body button:focus {
  outline-style: solid;
  outline-color: var(--alt);
  outline-inline-size: 0.2rem;
  outline-offset: 0.2em;
}
body :focus:not(:focus-visible),
body button:focus:not(:focus-visible),
body input:active {
  outline: none;
}
body img {
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

/* SCREEN READER ACCESSIBLE NO-DISPLAY */
.screen-reader {
  position: absolute;
  inline-size: 1px;
  block-size: 1px;
  padding: 0;
  margin: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
}

/* USER ANIMATION PREFERENCE */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  ::before,
  ::after {
    animation-delay: -1ms;
    animation-duration: 1ms;
    animation-iteration-count: 1;
    background-attachment: initial;
    scroll-behavior: auto;
    transition-duration: 0s;
    transition-delay: 0s;
  }
}
